@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
    .text-clamp{
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
    }
    .text-clamp-2{
        -webkit-line-clamp: 2;
    }
}

*,h4,h5,
*:before,
*:after{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    line-height: 1;
}